import React, { Component } from 'react'
import {Link} from "react-router-dom"
import PropTypes from 'prop-types';
import './ProductsListItem.css'
import {connect} from 'react-redux'





class ProductsListItem extends Component{

    static defaultProps = {
        isLiked:false
    }

    static propTypes = {
        name:PropTypes.string.isRequired,
        description:PropTypes.string,
        type:PropTypes.string,
        capacity:PropTypes.number,
        price:PropTypes.number.isRequired,
    }
    
    state = {
        productsCount: 1,
    }

    onDecrementClick = () => {
        this.setState(
            (prevState)=>({
                productsCount: prevState.productsCount -1
            })
        )
    }
    onIncrementClick() {
        this.setState(
            (prevState)=>({
                productsCount: prevState.productsCount +1
            })
        )
    }
    renderProductCont(){
        return(
            <div className='product-quantity'>
                <button 
                    onClick={this.onDecrementClick}
                    disabled={this.state.productsCount<=1}
                >-</button>
                <input type='text' value={this.state.productsCount} readOnly/>
                <button 
                    onClick={()=>this.onIncrementClick()}
                    disabled={this.state.productsCount>=10}
                >+</button>
            </div>
        )        
    }

    onLikeClick = () => {
        if(this.props.isLiked){
            this.props.dispatchDisLike(this.props.id)
        } else {
            this.props.dispatchLike(this.props.id)
        } 
    }

    render() {
        const{
            id,
            name,
            description = "no description ...",
            type,
            capacity,
            price,
            image,
            addProductToCart,
            isLiked,
        } = this.props

        return(
            <div className='products-list-item'>
                <div className="product-image">
                    <img src={image} alt={name}/>
                </div>

                <button onClick = {() => this.onLikeClick()}>
                    {isLiked ? <span>&#9829;</span>:<span>&#9825;</span>}
                </button>
                <div className='product-name'>
                    <Link to={`products/${id}`} alt={name}>{name}</Link>
                </div>

                <div className='product-description'>{description}</div>
                <div className='product-type'>{type}</div>
                <div className='product-capasity'>Capacity: {capacity} Gb</div>
                {this.renderProductCont()}
                <div className='product-price'>{price}</div>
                <button className='btn-add-to-cart'  
                 onClick={() => addProductToCart(id, this.state.productsCount)
                }
                >Add to cart</button>
            </div>
        )
    }
}

const mapStateToProps = (state, props) => 
{   return {
        isLiked:state.likeProducts[props.id]
    }
}
const mapDispatchToProps = (dispatch) => ({
    dispatchLike:(id) => dispatch({
        type: 'LIKE',
        id:id,
    }),
    dispatchDisLike:(id) => dispatch({
        type: 'DISLIKE',
        id:id,
    }),
    addProductToCart: (id,count) =>dispatch({
    type:'ADD_PRODUCT_TO_CART',
    id:id, //або просто id
    count:count,
    }),
})


export default connect(   
    mapStateToProps,
    mapDispatchToProps,
)(ProductsListItem)




