import React from 'react'
import "./cartPage.css"
// import {keys} from 'lodash'
// import products,{getProductsMap} from './../Products/products'
import CartTotal from './../../../common/features/Cart/CartTotal'
import CartProductList from './../../../common/features/Cart/CartProductList'
import CartProductListItemExtended from '../../../common/features/Cart/CartProductListItemExtended';
import {connect} from 'react-redux'


const CartPage = ({
	// cartData,
	productsInCart,
	removeProductFromCart,
	// productsMap=getProductsMap(products)
}) =>

<div className="cart text-center">

<CartProductList 
	productsInCart={productsInCart} 
	CartListComponent={CartProductListItemExtended}
	removeProductFromCart={removeProductFromCart}
/>

<CartTotal productsInCart={productsInCart} />

	{/* {
		keys(productsInCart).map(
			(productId)=>(
				<div key={productId}>
					<span> {productsMap[productId].name} </span>:{productsInCart[productId]}
				</div>
			)
		)
	} */}
	{/* <div>
		Total : ${
			keys(productsInCart).reduce((total, product) => {
				return total + (productsMap[product].price * productsInCart[product] )
			},0)
		}	
	</div> */}

</div>

const mapStateToProps = (state) =>{
	return {productsInCart:state.productsInCart}
}


export default connect (
	mapStateToProps,
)(CartPage)